import react from "react";
import './index.css';


const About = () => {
    return (
      <>
          <h3> The University of Bangalore is a premier university of the country with a venerable legacy and international acclaim for highest academic standards, diverse educational programmes, distinguished faculty, illustrious alumni, varied co-curricular activities and modern infrastructure. Over the many years of its existence, the University has sustained the highest global standards and best practices in higher education. Its long-term commitment to nation building and unflinching adherence to universal human values are reflected in its motto: 'Nishtha Dhriti Satyam' 'निष्ठा धृति सत्यम्' (Dedication, Steadfastness and Truth). <br />

          <br />Established in 1964 as a unitary, teaching and residential University by the Act of the then Central Legislative Assembly, a strong commitment to excellence in teaching, research and social outreach has made the University a role-model and trend setter for other universities. The President of India is the Visitor, the Vice-President is the Chancellor and the Chief Justice of the Supreme Court of India is the Pro-Chancellor of the University. Beginning with three colleges and 750 students, it has grown as one of the largest universities in India with 16 faculties, over 80 academic departments, an equal number of colleges and over seven lakh students. Over 500 programmes offered by the University are approved by Academic and Executive Councils, out of which 209 programmes are being considered for NAAC accreditation purpose. The rest being run in colleges are separately accredited. </h3>
      </>
    );
  };
  
  export default About;